import get from 'lodash/get'
import toInteger from 'lodash/toInteger'
import genPm from 'wsemi/src/genPm.mjs'
import AutoSequelize from './auto-sequelize.js'


/**
 * models
 *
 * @class
 * @param {Object} [opt={}] {}
 * @param {String} [opt.database=null] null
 * @param {String} [opt.username=null] null
 * @param {String} [opt.password=null] null
 * @param {String} [opt.dialect=null] null'mysql', 'mariadb', 'sqlite', 'postgres', 'mssql'
 * @param {String} [opt.directory='./models'] models'./models'
 * @param {String} [opt.host='localhost'] host'localhost'
 * @param {Integer} [opt.port=null] portnull
 * @returns {Promise} Promiseresolvemodelsreject
 */
function WAutoSequelize(opt = {}) {
    let pm = genPm()

    //params
    let database = get(opt, 'database', null)
    let username = get(opt, 'username', null)
    let password = get(opt, 'password', null)
    let dialect = get(opt, 'dialect', null)
    let directory = get(opt, 'directory', './models')
    let host = get(opt, 'host', 'localhost')
    let port = toInteger(get(opt, 'port', null))
    let options = {
        dialect,
        directory,
        host,
        port,
        // additional: {
        //     timestamps: false
        // },
    }

    //check
    if (!database) {
        pm.reject('need database')
        return pm
    }
    if (!username) {
        pm.reject('need username')
        return pm
    }
    if (!password) {
        pm.reject('need password')
        return pm
    }
    if (!dialect) {
        pm.reject('need dialect')
        return pm
    }
    if (!port) {
        pm.reject('need port')
        return pm
    }

    //auto
    let auto = new AutoSequelize(database, username, password, options)

    //run
    auto.run(function (err) {
        if (err) {
            pm.reject(err)
        }
        else {
            pm.resolve({
                tables: auto.tables,
                foreignKeys: auto.foreignKeys,
            })
        }
    })

    return pm
}


export default WAutoSequelize
