import events from 'events'
import Sequelize from 'sequelize'
import cloneDeep from 'lodash/cloneDeep'
import get from 'lodash/get'
import map from 'lodash/map'
import each from 'lodash/each'
import size from 'lodash/size'
import genPm from 'wsemi/src/genPm.mjs'
import genID from 'wsemi/src/genID.mjs'
import isarr from 'wsemi/src/isarr.mjs'
import isobj from 'wsemi/src/isobj.mjs'
import isbol from 'wsemi/src/isbol.mjs'
import strright from 'wsemi/src/strright.mjs'
import pmSeries from 'wsemi/src/pmSeries.mjs'
import WAutoSequelize from 'w-auto-sequelize/src/WAutoSequelize.mjs'
import importModels from './importModels.mjs'


/**
 * (Microsoft SQL)
 *
 * : modelid(primary key)sequelize
 *
 * @class
 * @param {Object} [opt={}] {}
 * @param {String} [opt.url='mssql://localhost:1433'] 'mssql://localhost:1433'
 * @param {String} [opt.db='worm'] 'worm'
 * @param {String} [opt.cl='test'] 'test'
 * @param {String} [opt.fdModels='models'] models(*.js)'models'
 * @param {Boolean} [opt.logging=false] sqlfalse
 * @param {String} [opt.pk='id'] 'id'
 * @param {Boolean} [opt.autoGenPK=true] pk(id)uuidtrue
 * @returns {Object} 
 */
function WOrmMssql(opt = {}) {


    //default
    if (!opt.url) {
        opt.url = 'mssql://localhost:1433'
    }
    if (!opt.db) {
        opt.db = 'worm'
    }
    if (!opt.cl) {
        opt.cl = 'test'
    }
    if (!opt.fdModels) {
        opt.fdModels = 'models'
    }
    if (strright(opt.url, 1) !== '/') {
        opt.url += '/' + opt.db
    }
    opt.logging = (opt.logging === true)
    if (!opt.pk) {
        opt.pk = 'id'
    }
    if (!isbol(opt.autoGenPK)) {
        opt.autoGenPK = true
    }


    //ee
    let ee = new events.EventEmitter()


    function SeqInit() {

        //sequelize
        let sequelize = new Sequelize(opt.url, {
            define: {
                timestamps: false
            },
            logging: opt.logging,
        })

        //mds, modelidpkpk, sequelize
        let mds = importModels(opt.fdModels, sequelize)

        return {
            sequelize,
            mds
        }
    }


    //Op
    let Op = Sequelize.Op


    /**
     * 
     *
     * @memberOf WOrmMssql
     * @param {Object} [find={}] 
     * @returns {Promise} Promiseresolvereject
     */
    async function select(find = {}) {

        function cvObj(o) {
            let oNew = {}
            each(o, (v, k) => {
                let kNew = k
                if (k.indexOf('$') >= 0) {
                    k = k.replace('$', '')
                    if (k === 'regex') {
                        kNew = Op.substring
                    }
                    else if (k === 'options') {
                        kNew = null
                    }
                    else if (k === 'nin') {
                        kNew = Op.notIn
                    }
                    else {
                        kNew = Op[k]
                    }
                }
                let vNew = v
                if (isarr(v)) {
                    vNew = cvArray(v)
                }
                else if (isobj(v)) {
                    vNew = cvObj(v)
                }
                if (kNew !== null) {
                    oNew[kNew] = vNew
                }
            })
            return oNew
        }

        function cvArray(o) {
            let oNew = []
            each(o, (v) => {
                let vNew = v
                if (isarr(v)) {
                    vNew = cvArray(v)
                }
                else if (isobj(v)) {
                    vNew = cvObj(v)
                }
                oNew.push(vNew)
            })
            return oNew
        }

        function cvFind(o) {
            let oNew = {}
            if (isobj(o)) {
                oNew = cvObj(o)
            }
            else {
                console.log('select: find is not object')
            }
            return oNew
        }

        //useFind
        let useFind = cvFind(find)

        //SeqInit
        let si = SeqInit()

        //md
        let md = si.mds[opt.cl]

        //findAll
        let rs = await md.findAll({
            where: useFind,
            raw: true,
        })

        //close
        si.sequelize.close()

        return rs
    }


    /**
     * _idinsert
     *
     * @memberOf WOrmMssql
     * @param {Object|Array} data 
     * @returns {Promise} Promiseresolvereject
     */
    async function insert(data) {

        //cloneDeep
        data = cloneDeep(data)

        //pm
        let pm = genPm()

        //SeqInit
        let si = SeqInit()

        //md
        let md = si.mds[opt.cl]

        //check
        if (!isarr(data)) {
            data = [data]
        }

        //check
        if (opt.autoGenPK) {
            data = map(data, function(v) {
                if (!v[opt.pk]) {
                    v[opt.pk] = genID()
                }
                return v
            })
        }

        //bulkCreate
        await md.bulkCreate(data)
            .then((res) => {
                //console.log('bulkCreate then',res)
                res = { n: size(data), ok: 1 }
                pm.resolve(res)
                ee.emit('change', 'insert', data, res)
            })
            .catch(({ original }) => {
                //console.log('bulkCreate catch',original)
                pm.reject({ n: 0, ok: 0 })
            })

        //close
        si.sequelize.close()

        return pm
    }


    /**
     * 
     *
     * @memberOf WOrmMssql
     * @param {Object|Array} data 
     * @param {Object} [option={}] {}
     * @param {boolean} [option.autoInsert=true] true
     * @param {boolean} [option.atomic=false] false
     * @returns {Promise} Promiseresolvereject
     */
    async function save(data, option = {}) {

        //cloneDeep
        data = cloneDeep(data)

        //autoInsert, atomic
        let autoInsert = get(option, 'autoInsert', true)
        let atomic = get(option, 'atomic', false)

        //pm
        let pm = genPm()

        //SeqInit
        let si = SeqInit()

        //md
        let md = si.mds[opt.cl]

        //check
        if (!isarr(data)) {
            data = [data]
        }

        //check
        if (opt.autoGenPK) {
            data = map(data, function(v) {
                if (!v[opt.pk]) {
                    v[opt.pk] = genID()
                }
                return v
            })
        }

        //tr
        let t = null
        let tr = {}
        if (atomic) {
            t = await si.sequelize.transaction()
            tr = {
                transaction: t
            }
        }

        //pmSeries
        await pmSeries(data, async function(v) {
            let pmm = genPm()

            //err
            let err = null

            //r
            let r
            if (v[opt.pk]) {
                //id

                //findOne
                r = await md.findOne({
                    where: { [opt.pk]: v[opt.pk] },
                    raw: true,
                })
                    .catch((error) => {
                        err = error
                    })

            }
            else {
                //id
                err = `${opt.pk} is invalid`
            }

            if (r) {
                //

                let rr = await md.update(v, {
                    where: { [opt.pk]: v[opt.pk] },
                    ...tr,
                })
                    .catch((error) => {
                        err = error
                    })

                if (rr) {
                    //console.log('update ', rr)
                    pmm.resolve({ n: 1, nModified: 1, ok: 1 })
                }
                else {
                    //console.log('update ', err)
                    pmm.resolve({ n: 0, nInserted: 0, ok: 1 })
                }

            }
            else {
                //

                //autoInsert
                if (autoInsert) {

                    //create
                    let rr = await md.create(v, tr)
                        .catch((error) => {
                            err = error
                        })

                    if (rr) {
                        //console.log('create ', rr)
                        pmm.resolve({ n: 1, nInserted: 1, ok: 1 })
                    }
                    else {
                        //console.log('create ', err)
                        pmm.resolve({ n: 0, nInserted: 0, ok: 1 })
                    }

                }
                else {
                    //console.log('findOne ', err)
                    pmm.resolve({ n: 0, nModified: 0, ok: 1 })
                }

            }

            pmm._err = err //eslint
            return pmm
        })
            .then((res) => {
                pm.resolve(res)
                ee.emit('change', 'save', data, res)
                if (t) {
                    //console.log('transaction commit')
                    return t.commit()
                }
            })
            .catch((res) => {
                pm.reject(res)
                if (t) {
                    //console.log('transaction rollback')
                    return t.rollback()
                }
            })

        //close
        si.sequelize.close()

        return pm
    }


    /**
     * 
     *
     * @memberOf WOrmMssql
     * @param {Object|Array} data 
     * @returns {Promise} Promiseresolvereject
     */
    async function del(data) {

        //cloneDeep
        data = cloneDeep(data)

        //pm
        let pm = genPm()

        //SeqInit
        let si = SeqInit()

        //md
        let md = si.mds[opt.cl]

        //check
        if (!isarr(data)) {
            data = [data]
        }

        //pmSeries
        await pmSeries(data, async function(v) {
            let pmm = genPm()

            //err
            let err = null

            //r
            let r
            if (v[opt.pk]) {
                //id

                //findOne
                r = await md.findOne({
                    where: { [opt.pk]: v[opt.pk] },
                    raw: true,
                })
                    .catch((error) => {
                        err = error
                    })

            }
            else {
                //id
                err = `${opt.pk} is invalid`
            }

            if (r) {
                //

                //destroy
                let rr = await md.destroy({
                    where: { [opt.pk]: v[opt.pk] },
                })
                    .catch((error) => {
                        err = error
                    })

                if (rr) {
                    //console.log('destroy ', rr)
                    pmm.resolve({ n: 1, nDeleted: 1, ok: 1 })
                }
                else {
                    //console.log('destroy ', err)
                    pmm.resolve({ n: 0, nDeleted: 0, ok: 1 })
                }

            }
            else {
                //console.log('findOne ', err)
                pmm.resolve({ n: 1, nDeleted: 1, ok: 1 })
            }

            pmm._err = err //eslint
            return pmm
        })
            .then((res) => {
                pm.resolve(res)
                ee.emit('change', 'del', data, res)
            })
            .catch((res) => {
                pm.reject(res)
            })

        //close
        si.sequelize.close()

        return pm
    }


    /**
     * del
     *
     * @memberOf WOrmMssql
     * @param {Object} [find={}] 
     * @returns {Promise} Promiseresolvereject
     */
    async function delAll(find = {}) {

        //pm
        let pm = genPm()

        //SeqInit
        let si = SeqInit()

        //md
        let md = si.mds[opt.cl]

        //destroy
        await md.destroy({
            where: find,
        })
            .then((res) => {
                res = { n: res, ok: 1 }
                pm.resolve(res)
                ee.emit('change', 'delAll', null, res)
            })
            .catch((res) => {
                pm.reject({ n: 0, ok: 1 })
            })

        //close
        si.sequelize.close()

        return pm
    }


    /**
     * models
     *
     * include from: [w-auto-sequelize](https://github.com/yuda-lyu/w-auto-sequelize)
     *
     * @memberOf WOrmMssql
     * @param {Object} [option={}] {}
     * @param {String} [option.database=null] null
     * @param {String} [option.username=null] null
     * @param {String} [option.password=null] null
     * @param {String} [option.dialect=null] null'mysql', 'mariadb', 'sqlite', 'postgres', 'mssql'
     * @param {String} [option.directory='./models'] models'./models'
     * @param {String} [option.host='localhost'] host'localhost'
     * @param {Integer} [option.port=null] portnull
     * @returns {Promise} Promiseresolvemodelsreject
     */
    function genModels(option = {}) {

        //default
        let def = {
            //database: 'database',
            username: 'username',
            password: 'password',
            dialect: 'mssql',
            //directory: './models',
            host: 'localhost',
            port: 1433,
        }

        //merge
        option = {
            ...def,
            ...option,
        }

        //database
        if (!option.database) {
            option.database = opt.db
        }

        //directory
        if (!option.directory) {
            option.directory = opt.fdModels
        }

        //WAutoSequelize
        return WAutoSequelize(option)

    }


    //bind
    ee.genModels = genModels
    ee.select = select
    ee.insert = insert
    ee.save = save
    ee.del = del
    ee.delAll = delAll


    return ee
}


export default WOrmMssql
